<template>
    <div class="box box-primary">
        <div class="box-header with-border">
            <h3 class="box-title">Thm tin u tin</h3>
        </div>
        <div class="box-body no-padding">
            <form v-on:submit.prevent="addProductPriority">
                <div class="box-body">
                    <input v-model="id_product" name="id_product" v-validate="'min_value:1'" type="text" style="display:none;">
                    <span class="error">{{ errors.first("id_product") }}</span>
                    <div v-if="id_product" class="product">
                        <div class="img">
                            <img :src="`${api}/image/product/thumbnail/${aboutProduct.image}`">
                        </div>
                        <div class="about">
                            <h2 class="name">{{ aboutProduct.name }}</h2>
                            <div class="bottom">
                                <h3 class="category">{{ aboutProduct.category }}</h3>
                                <h3 v-if="aboutProduct.price!=0" class="price">{{ number_format(aboutProduct.price,0) }} </h3> 
                                <h3 v-if="aboutProduct.price==0" class="price">Tho thun</h3>
                            </div>
                        </div>
                        <div class="controller">
                            <a v-on:click="id_product=0" class="btn btn-danger">Xo</a>
                        </div>
                    </div>
                    <div v-if="!id_product" class="form-group">
                        <button v-on:click="isListProduct = !isListProduct" v-if="!isListProduct" class="btn btn-success form-control">Chn sn phm</button>
                        <input v-if="isListProduct" v-on:keyup="search" type="text" placeholder="Tm theo tn sn phm..." class="form-control">
                        <div v-if="isListProduct" class="list-product">
                            <div v-for="item in products" :key="item.id" class="product">
                                <div class="img">
                                    <img :src="`${api}/image/product/thumbnail/${item.image}`">
                                </div>
                                <div class="about">
                                    <h2 class="name">{{ item.name }}</h2>
                                    <div class="bottom">
                                        <h3 class="category">{{ item.category }}</h3>
                                        <h3 v-if="item.price!=0" class="price">{{ number_format(item.price,0) }} </h3>
                                        <h3 v-if="item.price==0" class="price">Tho thun</h3>
                                    </div>
                                </div>
                                <div class="controller">
                                    <a v-on:click="chooseProduct(item)" class="btn btn-success">Chn</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label>Tnh thnh</label>
                        <Select2 v-model="province" name="province" v-validate="'required'" 
                        :options="dataProvince(provinces, 0)"
                        @change="changeProvince($event)" class="input" />
                        <span class="error">{{ errors.first("province") }}</span>
                    </div>
                    <transition name="district">
                    <div v-if="province != 0" class="form-group">
                        <label>Qun huyn</label>
                        <Select2 v-model="district" name="district" v-validate="'required'"
                        :options="dataDistrict(provinces, province)"
                        class="input"/>
                        <span class="error">{{ errors.first("district") }}</span>
                    </div>
                    </transition>
                    <div class="form-group">
                        <label>Chn chuyn mc hin th</label>
                        <Select2 v-model="category" name="category" v-validate="'required'"
                        :options="dataCategories(categories)"
                        class="input"/>
                        <span class="error">{{ errors.first("category") }}</span>
                    </div>
                    
                    <label>Chn khong thi gian hin th</label>
                    <div class="time-priority">
                        <div class="input-group">
                            <div class="input-group-addon">
                                <i class="fa fa-hourglass-start"></i>
                            </div>
                            <datetime type="datetime" v-model="dateFrom" :min-datetime="now()" format="HH:mm dd/MM/yyyy" class="form-control"></datetime>
                        </div>
                        <div class="input-group">
                            <div class="input-group-addon">
                                <i class="fa fa-hourglass-end"></i>
                            </div>
                            <select v-model="day" v-on:change="choose" class="form-control chooseday">
                                <option value="1">1 ngy</option>
                                <option value="3">3 ngy</option>
                                <option value="7">1 tun</option>
                                <option value="14">2 tun</option>
                                <option value="30">1 thng</option>
                            </select>
                        </div>
                    </div>
                    <div class="input-group" style="display: block;">
                        <input :value="sumPrice(provinces, categories, day)" style="color:red; font-weight:600;" type="text" class="form-control chooseday" disabled>
                    </div>
                </div>
                <div class="box-footer">
                    <button type="submit" class="btn btn-primary">Thm</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import Select2 from 'v-select2-component';
import moment from 'moment';
import { Validator } from 'vee-validate';

import { Datetime } from 'vue-datetime';
import 'vue-datetime/dist/vue-datetime.css';
import { Settings } from 'luxon';
Settings.defaultLocale = 'vi';

import {mapGetters} from "vuex";
export default {
    metaInfo(){
		return {
			title: "Thm tin u tin",
			htmlAttrs: {
				lang: 'vi',
				amp: true
			}
		}
	},
    components:{Select2,Datetime},
    data(){
        return {
            api: this.$store.state.domainApi,
            isListProduct: false,
            dataProduct: [],
            products: [],
            id_product: 0,
            aboutProduct: {},
            province: 0,
            district: 0,
            category: 0,
            dateFrom: this.now(),
            day:1,
            dictionary:{
                vi:{
                    custom:{
                        id_product:{
                            "min_value": "Vui lng chn sn phm u tin !"
                        }
                    }
                }
            }
        }
    },
    methods:{
        addProductPriority(){
            this.$validator.validate().then(valid => {
                if (valid) {
                    var dateFrom = new moment(this.dateFrom);
                    axios.post(this.api + '/auth/addproductpriority',{
                        id_product: this.id_product,
                        id_province: this.province,
                        id_district: this.district,
                        id_category: this.category,
                        date_from: dateFrom.format("YYYY-MM-DD HH:mm:ss"),
                        day: this.day
                    },{
                        headers:{ Authorization: `Bearer ${this.$store.state.token}` }
                    })
                    .then(response=>{
                        if(response.data.success){
                            
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$router.push({name: "user.productpriority"});
                        }else this.flash(response.data.message, 'error', {
                            timeout: 3000
                        });
                    });
                }
            });
        },
        now(){
            var date = new Date();
            return date.toISOString();
        },
        choose(e){
            console.log(e);
        },
        getPriceProvince(data, id){
            if(id==0) return data.reduce((res,obj)=>{
                if(obj.parent_id==0) res += obj.price;
                return res;
            },0);
            var item = data.find(e=>e.id==id);
            return item.price;
        },
        getPriceCategory(data, id){
            if(id==0) return data.reduce((res,obj)=>{
                if(obj.parent_id==0) res += obj.price;
                return res;
            },0);
            var item = data.find(e=>e.id==id);
            return item.price;
        },
        sumPrice(data1, data2, day){
            var priceProvince = 0;
            if(this.province==0) priceProvince = this.getPriceProvince(data1, 0);
            else priceProvince = this.getPriceProvince(data1, this.district);
            return this.number_format(priceProvince + this.getPriceCategory(data2, this.category) + Number(day)*1000, 0) + ' ';
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        changeProvince(val){
            this.district = val;
        },
        chooseProduct(item){
            this.aboutProduct = item;
            this.id_product = item.id;
        },
        search(event){
            this.products = this.dataProduct.filter(e=>e.name.toLocaleLowerCase().indexOf(event.target.value.toLocaleLowerCase()) != -1);
        },
        sort(data, parent_id, str=""){
            var array = [];
            for(var item of data){
                if(item.parent_id == parent_id){
                    item.text = str+item.name;
                    array.push(item);
                    var items = this.sort(data, item.id, str+"----|");
                    array = array.concat(items);
                }
            }
            return array;
        },
        dataCategories(data){
            return [{id: 0, text: 'Tt c danh mc'}].concat(this.sort(data,0));
        },
        filter(data, lv, arr){
            var reslust = data.filter(e=>e.parent_id == lv).map(e=>{e.text = e.name; return e});
            return arr.concat(reslust);
        },
        dataProvince(data, lv){
            return this.filter(data, lv, [{id: 0, text: 'Ton quc'}]);
        },
        dataDistrict(data, lv){
            return this.filter(data, lv, [{id: lv, text: 'Tt c qun huyn'}]);
        }
    },
    computed:{
        ...mapGetters({
            categories:'getCategories',
            provinces: 'getProvince'
        })
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');

        axios.get(this.api + '/public/store/product/all',{
            params:{
                url_store: this.$store.state.user.url_store
            }
        })
        .then(response=>{
            this.dataProduct = response.data;
            this.products = response.data;
        });
    }
}
</script>

